<template>
  <section>
    <SubTitle title="Backup copies (snapshots)"/>
    <div class="buttonsContainer" v-if="!isLocalLoading">
      <span class="card p10 hoverNegative" @click="rewriteOnlyList" style="justify-items: center">
        <img src="/img/rec.png" style='width:50px;height:50px' alt="rec"/>
      </span>
    </div>
    <div style="margin-bottom: 30px;">
      <div class="card2 mt20 buttonsContainer wrap">
        <button
          v-for="item in ['now',...cTabList]"
          :key="item"
          :class="['tabButton', { active: currentTab === item}]"
          @click="changeTab(item)"
        >
          {{item}}
        </button>
        <button class="tabButton" @click="createReserveItem">+</button>
        <button class="delBox hover" @click="deleteLoadedItem">
          x
        </button>
      </div>
      <div class="card2 mt20 buttonsContainer wrap" v-if="!isLocalLoading">
        <button
          v-for="item in cPageList"
          :key="item"
          :class="['tabButton', { active: currentListName === item}]"
          @click="currentListName = item"
        >
          {{item}}
        </button>
      </div>
    </div>
    <div class="card2 mt20 buttonsContainer" v-if="!isLocalLoading">
      <span style="font-size: 35px;">{{$store.state.currentInfo}}</span>
      <span class="ml20 card p10" v-if="currentListName==='tradeList' || currentListName==='trashList'">
          refresh prices
          <toggle-button
            :color="{checked: 'green', unchecked: 'rgba(0,0,0,0.5)'}"
            @change="refreshPrices"
            style="border:1px solid grey; border-radius: 12px"
            :value="isFreshPrices"
          />
        </span>
    </div>
    <div class="card2 mt20" v-if="!isLocalLoading">
      <Main :list="$store.state.refreshedReserveTradeList.length?$store.state.refreshedReserveTradeList : cObj['tradeList']" style="padding-top:50px" v-if="currentListName==='tradeList'"/>
      <AlarmList :alts-list="cObj['alarmAltsList']" :events-list="cObj['alarmEventsList']" v-if="currentListName==='alarmAltsList'"/>
      <Potential :list="cObj['potentialList']" v-if="currentListName==='potentialList'"/>
      <Trash :list="$store.state.refreshedReserveTradeList.length?$store.state.refreshedReserveTradeList : cObj['trashList']" v-if="currentListName==='trashList'"/>
      <History :list="cObj['historyList']" v-if="currentListName==='historyList'"/>
      <Hyp :list="cObj['hypotheticalLists']" v-if="currentListName==='hypotheticalLists'"/>
      <FundList :list="cObj['fundList']" v-if="currentListName==='fundList'"/>
      <ActivityList :list="cObj['lastActivityList']" v-if="currentListName==='lastActivityList'"/>
      <Weight :list="cObj['weightList']" v-if="currentListName==='weightList'"/>
      <div class="card pr">
        <textarea class="rareArea" v-model="cTextarea"/>
      </div>
    </div>
    <div class="fc">
      <img src="/img/loading9.gif" style='width:70px' alt="loading" v-if="isLocalLoading"/>
    </div>
  </section>
</template>

<script>
  import Main from './Main'
  import generateRandomId from '../../helpers/generateRandomId';
  import SubTitle from '../../components/SubTitle';
  import AlarmList from '../../components/Alarm/AlarmList';
  import NotesComp from '../../components/notes/NotesComp';
  import NotesList from '../../components/notes/NotesList';
  import History from './History';
  import Hyp from './Hyp';
  import Potential from './Potential';
  import Trash from './Trash';
  import FundList from '../../components/fund/FundList';
  import ActivityList from '../../components/ActivityList';
  import Weight from './Weight';
  import firebase from 'firebase/compat';

  export default {
    components: {
      Weight,
      ActivityList,
      FundList,
      Trash,
      Potential,
      Hyp,
      History,
      NotesList,
      NotesComp,
      AlarmList,
      SubTitle,
      Main,
    },
    data() {
      return {
        isLocalLoading:false,
        currentTab: 'now',
        currentListName: 'tradeList',
        loadedItem:null,
        idsArr:[],
        isFreshPrices:false
      };
    },
    computed:{
      cTextarea(){
        return JSON.stringify(this.cObj[this.currentListName])
      },
      cObj(){
        if(this.currentTab==='now')return this.$store.state[this.$store.state.isAK?'infoAK':'infoMain']
        if(this.loadedItem) {
          return this.loadedItem[this.$store.state.isAK?'infoAK':'infoMain']
        }else return []
      },
      cTabList(){
        if(this.idsArr.length){
          return this.idsArr.map(item=>item.date)
        }else return []
      },
      cPageList(){
        if(this.loadedItem){
          return Object.keys(this.loadedItem[this.$store.state.isAK?'infoAK':'infoMain'])
        }else if(this.$store.state[this.$store.state.isAK?'infoAK':'infoMain']){
          return Object.keys(this.$store.state[this.$store.state.isAK?'infoAK':'infoMain'])
        }else return []
      },
    },
    methods:{
      refreshPrices(){
        if(this.$store.state.refreshedReserveTradeList.length){
          this.$store.commit('resetPricesOfTemporaryTradeList')
        }else this.$store.dispatch('loadOnlyProvidedCoins', { list:this.cObj[this.currentListName] })
      },
      async changeTab(item){
        this.$store.commit('resetPricesOfTemporaryTradeList')
        this.isFreshPrices = false
        this.currentTab = item;
        if(item==='now')return//если первая вкладка то ничего не загружать
        console.log('loading reserved item...')
        const id = this.idsArr.find(iitem=>iitem.date===item).id
        this.isLocalLoading=true
        try{
          // const uid = await context.dispatch('fetchUserId')
          const uid = this.$store.state.userId
          const answer = (await firebase
            .database()
            .ref(`/users/${uid}/reserve/${id}`
            ).once('value')).val()
            this.loadedItem = answer
            // console.log('answer:',answer)
          this.$store.commit('setReserveFonUrl',{rUrl:answer[this.$store.state.isAK?'infoAK':'infoMain'].service?answer[this.$store.state.isAK?'infoAK':'infoMain'].service.fonUrl:answer[this.$store.state.isAK?'infoAK':'infoMain'].fonUrl})
        }catch(error){
          this.$store.commit('activatePopupFB',{text:'reserve item failed'+ error});
          throw error
        }
        this.isLocalLoading=false
      },
      createReserveItem(){
        console.log('creating loadedItem local...')
        this.loadedItem = {
          id: generateRandomId(),
          date: new Date().toLocaleString('en-US'),
          infoMain:this.$store.state.infoMain,
          infoAK:this.$store.state.infoAK,
        }
        const idItem = {
          id:this.loadedItem.id,
          date:this.loadedItem.date
        }
        this.idsArr = [...this.idsArr,idItem]
        this.currentTab = this.loadedItem.date//поставить таб на посл итем
        this.$store.commit('activatePopup', { text:'reserve created'});
        this.$store.dispatch('updateReserveInFB',{reserveItem:this.loadedItem,id:this.loadedItem.id, newArrOfIds:this.idsArr});
      },
      deleteLoadedItem(){
        const ok = confirm('delete?')
        if(ok){
          console.log('deleting loadedItem local...')
          this.idsArr = this.idsArr.filter(item=>item.id!==this.loadedItem.id)//удалить имя из из списка имен
          this.currentTab = 'now'//поставить таб на начало
          this.$store.commit('activatePopup', { text:'reserve deleted'});
          this.$store.dispatch('updateReserveInFB',{reserveItem:{},id:this.loadedItem.id, newArrOfIds:this.idsArr});
        }
      },
      rewriteOnlyList(){
        const ok = confirm('record this '+this.currentListName+`${this.currentListName==='tradeList'?' and also trash + activity lists, as they are connected':''}`+'?')
        if(ok){
          this.$store.commit('setRare',{
            list:this.cObj[this.currentListName],
            listName:this.currentListName,
          })
          if(this.currentListName==='tradeList'){//connected to tradeList
            this.$store.commit('setRare',{
              list:this.cObj.trashList,
              listName:'trashList',
            })
            this.$store.commit('setRare',{
              list:this.cObj.lastActivityList,
              listName:'lastActivityList',
            })
          }
        }
      },
    },
    async mounted(){
      console.log('fetching idsArr...')
        this.isLocalLoading=true
        try{
        // const uid = await context.dispatch('fetchUserId')
        const uid = this.$store.state.userId
          const answer = (await firebase
          .database()
          .ref(`/users/${uid}/reserve/ids`)
          .once('value'))
          .val()
        if(!Array.isArray(answer)){
          this.idsArr = []
        }else {
          this.idsArr = answer
        }
      }catch(error){
          this.$store.commit('activatePopupFB',{text:'reserves keys loading failed'+ error});
        throw error
      }
      this.isLocalLoading=false
      this.currentTab = 'now'
    },
    beforeDestroy() {
      this.$store.commit('setReserveFonUrl',{rUrl:null})
      this.$store.commit('resetPricesOfTemporaryTradeList')
    },
  };
</script>

<style scoped>
  .card2{
    background-color: rgba(0, 0, 0, 0.2);
    padding: 10px;
    border-radius: 7px;
    border:1px solid rgba(255, 255, 255, 0.1);
    box-shadow: 0 0 5px rgba(0,0,0,0.95);
  }
  .saveBtn{
    font-size: 18px;
    display: block;
    margin:10px auto;
  }

  .wrap{
    flex-wrap: wrap;
  }
  .delBox{
    color:red;
    background-color: transparent;
    width:20px;
    font-size: 10px;
    border:none;
  }
  .buttonsContainer{
    display: flex;
    justify-content: center;
  }
  .rareArea{
    width:97%;
    height:700px;
    margin:10px;
    padding:5px;
  }
  @keyframes bounce-in {
    0% {
      transform: scale(0);
    }
    50% {
      transform: scale(1.5);
    }
    100% {
      transform: scale(1);
    }
  }

  @media only screen and (max-width: 768px) {

    .buttonsContainer{
      display: flex;
      flex-wrap: wrap;
    }

  }

</style>
