<template>
  <section style="padding-top:50px">
    <div class="container card">
      <ul class="listBookmarks">
        <li :class="['bookmarkBtn radius', { active: currentTab === 'Дневник' }]" @click="currentTab = 'Дневник'" v-if="listName === 'cosmo'">
          Дневник
        </li>
        <li
          v-for="item in computedTabs"
          :key="item"
          :class="['bookmarkBtn', { active: currentTab === item}]"
          @click="tabClick(item)"
        >
          {{ item }}
        </li>
        <div style="width:100%;padding-bottom: 20px">
          <button class="addBtn hover" @click="addTab">+</button>
        </div>
<!--        <button class="addBtn hover" @click="saveTab" v-if="currentTab!=='Дневник'">-->
<!--          save-->
<!--        </button>-->

      </ul>
      <div class="listBody pr" :class="{'cosmobg':$route.name==='cosmo'}">
        <NotesList :tab-list="computedTabList" :current-tab.sync="currentTab" :list-name="listName" :is-item-open="isItemOpen"/>
        <button class="addBtn hover" @click="addItem" style="margin-bottom: 20px;" v-if="currentTab!=='Дневник'">+</button>
        <button class="delBtn" @click="deleteTab" v-if="currentTab!=='Дневник'">X</button>
      </div>
    </div>
<!--    <div class="fc">-->
<!--      <form @submit.prevent="addItem" class="card p10" v-if="currentTab!=='Дневник'">-->
<!--        <input type="text" class="addInput mr20" name="частота" placeholder="add item" v-model='itemName' required>-->
<!--        <button type="submit" class="addBtn">add</button>-->
<!--      </form>-->
<!--    </div>-->
  </section>
</template>

<script>
  import NotesList from './NotesList';
  import generateRandomId from '../../helpers/generateRandomId';
  export default {
    props:['listName'],
    components: {
      NotesList,
    },
    data(){
      return{
        currentTab: this.$store.state.infoMain[this.listName].length?this.$store.state.infoMain[this.listName][0].title:null,
        currentSubTab: '',
        // newTabTitle:null,
        isItemOpen:false,
        itemName:'',
      }
    },
    computed:{
      computedTabs() {
        return this.$store.state.infoMain[this.listName].length?this.$store.state.infoMain[this.listName].map(item=>item.title):[];
      },
      computedTabList(){
        return this.$store.state.infoMain[this.listName].length?this.$store.state.infoMain[this.listName].find(item=>item.title === this.currentTab):[]
      }
    },
    methods:{
      addTab() {
        const title = prompt('Название закладки')
        const item = {
          title,
          id:generateRandomId(),
          items:[]
        }
        if (title) {
          this.$store.commit('addNotesTab', { item,listName:this.listName})
          this.currentTab = title;
        }
      },
      addItem() {
        const name = prompt('Название заметки')
        const itemObj = {
          name,
          imgUrl:'/img/'+this.listName+'.png',
          text:'',
          id:generateRandomId(),
        }
        this.$store.commit('addNotesItem', {itemObj,tab:this.currentTab,listName:this.listName})
      },
      deleteTab(){
        const ok = confirm('delete tab?')
        if(ok){
          this.$store.commit('deleteNotesTab', { tabName:this.currentTab, listName:this.listName})
          let lastTab
          if(this.$store.state.infoMain[this.listName].length>0){
            lastTab = this.$store.state.infoMain[this.listName][this.$store.state.infoMain[this.listName].length-1].title
          }else lastTab = ''
          this.currentTab = lastTab ;//сдвинуть таб посл стр
        }
      },
      saveTab(){
        this.$store.dispatch('updateFB',{name:this.listName,info:this.$store.state.currentInfo});
      },
      // addItem() {
      //   const itemObj = {
      //     name:this.itemName,
      //     imgUrl:'/img/'+this.listName+'.png',
      //     text:'',
      //     id:generateRandomId()
      //   }
      //   this.$store.commit('addNotesItem', {itemObj,tab:this.currentTab,listName:this.listName})
      //   this.itemName = ''
      // },
      tabClick(tab){
        this.currentTab = tab;
        this.isItemOpen = false;
      },
      subTabClick(tab){
        this.currentSubTab = tab;
        // this.isItemOpen = false;
      }
    }
  };
</script>

